.pageWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  padding: 24px;
  max-width: 1024px;
}

.table {
  width: 100%;
  margin-bottom: 24px;
  border: 1px solid var(--chakra-colors-gray-600);
  border-radius: 8px;
}

.tableHeader {
  position: sticky;
  z-index: 2;
  top: 0;
  background: var(--chakra-colors-gray-800);
}

.tableWidget {
  border-radius: 12px;
  > * > * {
    width: 100%;
  }
}
